(function () {
    'use strict';

    angular
        .module('vk')
        .controller('HeyTeaMyPayCodeController', HeyTeaMyPayCodeController);

    /** @ngInject */
    HeyTeaMyPayCodeController.$inject = ['$interval', '$location', '$sessionStorage', 'MyService', 'JsBarcode', 'SessionStorageService'];

    function HeyTeaMyPayCodeController($interval, $location, $sessionStorage, MyService, JsBarcode, SessionStorageService) {
        var cardId = $sessionStorage.cardId;
        var token = $sessionStorage.token;
        var storeId = $sessionStorage.storeId;
        var vm = this;
        vm.refresh = refresh;
        vm.goBack = goBack;

        function goBack() {
            $location.url('/heytea-my?storeId=' + storeId);
        }

        function refresh() {
            if(cardId =="" ||cardId=="null"||!cardId){
                return
            }
            MyService.getPayCode(cardId, token).then(function (data) {
                if (data.code != 200) {
                    // alert(data.message);
                    return
                }
                vm.qrcode = data.data.code;
                SessionStorageService.setOneSessionStorage("payCode", data.data.code);
                JsBarcode("#barcode")
                    .CODE128C(data.data.code, {width:2.5, fontSize: 25, textMargin: 0, fontWeight: 500, textPosition: "bottom"})
                    .render();
            }, function (e) {
                //error msg
            });
        }

        //每三分钟更新一次
        refresh();
        $interval(function () {
                refresh()
            }
            , 180000
        );

    }
})();

(function () {
    'use strict';

    angular
        .module('vk')
        .controller('HeyTeaMyFeedbackController', HeyTeaMyFeedbackController);

    /** @ngInject */
    HeyTeaMyFeedbackController.$inject = ['$location', 'UtilService', 'MyService', '$sessionStorage'];

    function HeyTeaMyFeedbackController($location, UtilService, MyService, $sessionStorage) {
        var cardId = $sessionStorage.cardId;
        var token = $sessionStorage.token;
        var storeId = $sessionStorage.storeId;
        var vm = this;
        vm.memoMaxInput = 30;
        vm.memoInputCount = 0;
        vm.onTextChange = onTextChange;
        vm.submit = submit;
        vm.goBack = goBack;

        function goBack() {
            $location.url('/heytea-my?storeId=' + storeId);
        }

        function onTextChange() {
            var count = vm.feedback.split("");
            vm.memoInputCount = count.length;
            if (count.length > vm.memoMaxInput) {
                count = count.slice(0, vm.memoMaxInput);
                vm.memoInputCount = vm.memoMaxInput;
            }
            vm.feedback = count.join("");
        }

        function submit() {
            if (!vm.feedback) {
                alert("有信息未填写");
                return
            }
            MyService.postMyFeedback(cardId, storeId, token, vm).then(function (data) {
                if (data.code != 200) {
                    alert(data.message);
                    return
                }
                vm.feedback = "";
                vm.memoInputCount = 0;

                UtilService.showToast(vm, "反馈成功");

            }, function (e) {
                //error msg
            });
        }
    }
})();

